.header {
  position: fixed;
  width: 100%;
  z-index: 100;
  background: var(--white-color);
}
.header__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 32px;
  border-bottom: 1px solid var(--black-color);
  padding-left: 8px;
}
.header__container-logo {
  position: relative;
  box-sizing: content-box;
  grid-column: 1 span;
}
.header__container-title {
  position: relative;
  grid-column: 2 span;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__container-title::after {
  content: "";
  position: absolute;
  inset: -16px;
  top: 0;
  bottom: 0;
  background: var(--black-color);
  z-index: 3;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.header__container-title:last-of-type::after {
  right: 0;
}
.header__container-title:hover::after {
  opacity: 1;
}
.header__container-title:hover .title__h6 {
  z-index: 5;
  color: var(--white-color);
}
.header__container-burger {
  position: relative;
  display: none;
  align-items: center;
  justify-content: center;
  grid-column: 2 span;
}
.header__link {
  color: var(--black-color);
}
.header__link-logo {
  display: block;
  padding-top: 11px;
  padding-bottom: 10px;
}
.header__picture {
  width: fit-content;
}
.header .line-horizontal-bottom-hidden {
  display: none;
}
.header__burger {
  display: none;
}

@media (max-width: 1024px) {
  .header__container-logo {
    grid-column: 2 span;
  }
}
@media (max-width: 768px) {
  .header__container {
    column-gap: 16px;
  }
  .header__container-logo {
    grid-column: 1 span;
  }
  .header__container-title {
    grid-column: 3 span;
  }
  .header__container-title::after {
    right: -8px;
    left: -8px;
  }
}
@media (max-width: 616px) {
  .header__container {
    position: relative;
  }
  .header__container-logo {
    grid-column: 2 span;
  }
  .header__container-title {
    grid-column: 6 span;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .header__container-title:first-of-type::after {
    left: 0;
  }
  .header .line-horizontal-bottom-hidden {
    display: block;
    bottom: 37px;
  }
}
@media (max-width: 375px) {
  .header__container-title {
    grid-column: 8 span;
    padding: 0;
  }
  .header__container-title:last-of-type::after {
    right: -8px;
  }
  .header__container-burger {
    display: flex;
  }
  .header__email {
    display: none;
  }
  .header .line-horizontal-bottom-hidden {
    display: none;
  }
}
.nav {
