  backdrop-filter: blur(3px);
  align-items: flex-end;
  opacity: 0;
  visibility: hidden;
}
.menu__container {
  position: relative;
  padding: 40px;
  width: 100%;
  height: fit-content;
  background: var(--white-color);
}
.menu__button-close {
  position: absolute;
  top: 16px;
  right: 16px;
}
.menu .nav__list {
  flex-direction: column;
  row-gap: 20px;
}
.menu .nav__item {
  margin-right: 0;
}
.menu .nav__link {
  position: relative;
  color: var(--black-color);
  font-size: 18px;
  line-height: 120%;
  font-weight: 700;
}
.menu .nav__link:hover {
  color: var(--black-color);
}

@media (max-width: 375px) {
  .menu {
    display: flex;
  }
}