html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

.page {
	height: 100%;
	font-family: var(--font-family, sans-serif);
	-webkit-text-size-adjust: 100%;
}

.page__body {
	margin: 0;
	min-width: 320px;
	min-height: 100%;
	font-size: 16px;
}

picture {
	display: flex;
}

img {
	height: auto;
	max-width: 100%;
	object-fit: cover;
}

a {
	text-decoration: none;
}

.site-container {
	overflow: hidden; //     position: sticky -   
}

.is-hidden {
	display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
	border: none;
	padding: 0;
	background-color: transparent;
	cursor: pointer;
}

.list-reset {
	list-style: none;
	margin: 0;
	padding: 0;
}

.input-reset {
	-webkit-appearance: none;
	appearance: none;
	border: none;
	border-radius: 0;
	background-color: #fff;

	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	border: 0;
	padding: 0;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.container {
	margin: 0 auto;
	padding: 0 var(--container-offset);
	max-width: var(--container-width);
	&-1c {
		max-width: 1140px;
	}
}

.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}

.centered {
	text-align: center;
}

.dis-scroll {
	position: fixed;
	overflow: hidden;
	width: 100%;
	height: 100vh;
	overscroll-behavior: none;
}

.page--ios .dis-scroll {
	position: relative;
}

@media (max-width: 425px) {
	.container {
		padding-left: 16px;
		padding-right: 16px;
	}
}

.text-blue {
	color: var(--1-blue-color);
	font-weight: 700;
}

@media (max-width: 768px) {
	.main-allcases {
		margin-top: 90px;
	}
}

@media (max-width: 616px) {
	.main-allcases {
		margin-top: 170px;
	}
}

@media (max-width: 540px) {
	.main-allcases {
		margin-top: 170px;
		& .cases__title {
			font-size: 50px;
			line-height: 50px;
		}
	}
}

@media (max-width: 480px) {
	.main-allcases {
		margin-top: 130px;
	}
}

@media (max-width: 375px) {
	.main-allcases {
		margin-top: 90px;
	}
}
